  export default "import { ConfigProvider, GPTVis } from '@antv/gpt-vis';\nimport React from 'react';\n\nconst content = `\n ~~~vis-chart\n{\n  \"type\": \"mind-map\",\n  \"data\": {\n    \"name\": \"Âè∞È£éÂΩ¢ÊàêÁöÑÂõ†Á¥†\",\n    \"children\": [\n      {\n        \"name\": \"Ê∞îË±°Êù°‰ª∂\",\n        \"children\": [\n          { \"name\": \"Ê∏©ÊöñÁöÑÊµ∑Ê∞¥\" },\n          { \"name\": \"Ê∞îÂéãÂàÜÂ∏É\" },\n          { \"name\": \"ÊπøÂ∫¶Ê∞¥Âπ≥\" },\n          { \"name\": \"È£éÁöÑÂàáÂèò\" }\n        ]\n      },\n      {\n        \"name\": \"Âú∞ÁêÜÁéØÂ¢É\",\n        \"children\": [\n          { \"name\": \"Â§ßÈôÜÊû∂ÁöÑÂΩ¢Áä∂‰∏éÊ∑±Â∫¶\" },\n          { \"name\": \"Êµ∑Ê¥ãÊöñÊµÅÁöÑÂàÜÂ∏É\" },\n          { \"name\": \"ÁÉ≠Â∏¶Âú∞Âå∫ÁöÑÊ∞îÂÄôÁâπÂæÅ\" },\n          { \"name\": \"Â≤õÂ±øÁöÑÂΩ±Âìç\" }\n        ]\n      }\n    ]\n  }\n}\n~~~`;\n\nconst mindmapConfig = {\n  type: 'linear',\n  direction: 'right',\n  behaviors: (behaviors) => [\n    // console.log(behaviors) üëâ [{ key: 'zoom-canvas', type: 'zoom-canvas' }, { key: 'drag-canvas', type: 'drag-canvas' }]\n    // ÈªòËÆ§ÂêØÁî®‰∏§‰∏™‰∫§‰∫íÔºåÁº©ÊîæÁîªÂ∏ÉÂíåÊãñÊãΩÁîªÂ∏É„ÄÇÊ≠§Â§ÑÁßªÈô§Áº©ÊîæÁîªÂ∏ÉÂπ∂Ê∑ªÂä†ÊãñÊãΩÂÖÉÁ¥†\n    ...behaviors.filter((behavior) => behavior.key !== 'zoom-canvas'),\n    {\n      key: 'drag-element',\n      type: 'drag-element',\n    },\n  ],\n  transforms: (prev) => [\n    // ÈªòËÆ§ËäÇÁÇπÊîØÊåÅÊäòÂè†Â±ïÂºÄÔºåÊ≠§Â§ÑÁ¶ÅÁî®\n    ...prev.filter((transform) => transform.key !== 'collapse-expand-react-node'),\n    {\n      ...prev.find((transform) => transform.key === 'collapse-expand-react-node'),\n      enable: false,\n    },\n  ],\n};\n\nexport default () => (\n  <ConfigProvider\n    components={{\n      MindMap: mindmapConfig,\n    }}\n  >\n    <GPTVis>{content}</GPTVis>\n  </ConfigProvider>\n);\n";;
  