  export default "import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';\nimport { Bubble, type BubbleProps } from '@ant-design/x';\nimport { ConfigProvider, GPTVis, VisText } from '@antv/gpt-vis';\nimport React from 'react';\n\nconst markdownContent = `\n<vis-text type=\"metric_name\">DAU</vis-text> ÊòØ <vis-text type=\"metric_value\">1.23‰∫ø</vis-text>ÔºåÁéØÊØîÊò®Êó• <vis-text type=\"delta_value_pos\">80‰∏á</vis-text>(<vis-text type=\"ratio_value_pos\">2.3%</vis-text>)Ôºå<vis-text type=\"action_recommendation\">Âª∫ËÆÆ‰øùÊåÅÂÖ≥Ê≥®</vis-text>„ÄÇ\n`;\n\nconst bgStyle = {\n  display: 'grid',\n  gridGap: '20px 0',\n  background: '#f7f7f7',\n  padding: 20,\n  borderRadius: 8,\n  overflow: 'auto',\n};\n\nconst RenderMarkdown: BubbleProps['messageRender'] = (content) => (\n  <GPTVis components={{ 'vis-text': VisText }}>{content}</GPTVis>\n);\n\nexport default () => (\n  <ConfigProvider\n    components={{\n      VisText: {\n        __statics: {\n          'icon:antd-up': CaretUpOutlined,\n          'icon:antd-down': CaretDownOutlined,\n        },\n        delta_value_pos: {\n          color: '#13A8A8',\n          prefix: 'icon:antd-up',\n        },\n        delta_value_neg: {\n          color: '#FA541C',\n          prefix: 'icon:antd-down',\n        },\n        ratio_value_pos: {\n          color: '#13A8A8',\n          prefix: '‚Üë',\n        },\n        ratio_value_neg: {\n          color: '#FA541C',\n          prefix: '‚Üì',\n        },\n        action_recommendation: {\n          backgroundColor: 'antiquewhite',\n        },\n      },\n    }}\n  >\n    <div style={bgStyle}>\n      <Bubble\n        content={markdownContent}\n        messageRender={RenderMarkdown}\n        avatar={{\n          src: 'https://mdn.alipayobjects.com/huamei_je4oko/afts/img/A*6LRBT7rjOkQAAAAAAAAAAAAADsZ-AQ/original',\n        }}\n        variant=\"shadow\"\n        styles={{ content: { background: '#fff' } }}\n      />\n    </div>\n  </ConfigProvider>\n);\n";;
  